<template>
  <div id="main">
    <ul id="list">
      <li>
        <span>序列</span><span>歌曲</span><span>歌手</span><span>专辑</span>
      </li>
      <li
        @click="player(song.id, index)"
        v-for="(song, index) in result"
        :key="index"
      >
        <span>{{ index + 1 }}</span>
        <span>{{ song.name }}</span>

        <span v-if="isList">{{ song.artist }}</span>
        <span v-if="isList">{{ song.album }}</span>

        <span v-if="isSearch">{{ song.ar[0].name }}</span>
        <span v-if="isSearch">{{ song.al.name }}</span>
      </li>
    </ul>
    <div id="img">
      <img :src="imgSrc" alt="" />
      <h2 v-if="result[this.isPlayIndex] != null">
        {{ result[this.isPlayIndex].name }}
      </h2>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      id: 0,
      // data: {},
      array: [],
    };
  },
  computed: {
    isSearch() {
      return this.$store.state.isSearch;
    },
    isList() {
      return this.$store.state.isList;
    },
    url() {
      return this.$store.state.url;
    },
    idArray() {
      return this.$store.state.idArray;
    },
    isPlayIndex() {
      return this.$store.state.isPlayIndex;
    },
    imgSrc() {
      return this.$store.state.imgSrc;
    },
  },
  props: ["result", "sendAsk"],
  methods: {
    // getid() {
    //   for (let index = 0; index < this.idArray.length; index++) {
    //     const element = this.idArray[index];
    //     axios.get(`https://music.cyrilstudio.top/song/url?id=${element}`).then(
    //       (response) => {
    //         this.array[index] = response.data.data[0].url;
    //       },
    //       (error) => {
    //         console.log("接收失败", error);
    //       }
    //     );
    //   }
    //   this.$bus.$emit("ask", this.array);
    // },
    player(data, index) {
      this.id = data;
      // this.getid();
      axios
        .get(`https://music.cyrilstudio.top/song/url?id=${this.id}`)
        .then((response) => {
          this.$store.commit("CHANGEURL", response.data.data[0].url);

          this.$store.commit("CHAGESONGINDEX", index);
        });
    },
  },
  watch: {
    //监听id变化，主要是防止索引一样不触发回调函数，等待优化
    id() {
      if (this.isSearch) {
        this.$store.commit(
          "CHANGEIMGURL",
          this.result[this.isPlayIndex].al.picUrl
        );
      } else {
        this.$store.commit("CHANGEIMGURL", this.result[this.isPlayIndex].cover);
      }
    },
  },
};
</script>

<style scoped>
#img {
  flex: 2;
  margin-left: 5%;
  color: rgba(225, 225, 225, 0.8);
}
#img h2 {
  width: 70%;
}
#img img {
  width: 50%;
  max-height: 100%;
  margin-bottom: 20px;
}

#main {
  display: flex;
  justify-content: flex-start;
  height: 500px;
}
#list {
  height: 100%;
  margin-left: 15%;
  flex: 6;
  overflow: scroll;
}

#list li {
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: rgba(225, 225, 225, 0.8);
  font-weight: 200;
  font-size: 18px;
  /* width: 60%; */
  /* margin: 10px; */
  height: 50px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.4);
}
#list li:hover {
  cursor: pointer;
  background-color: rgba(128, 128, 128, 0.6) !important;
}
#list span:nth-child(1) {
  flex: 0.3;
}
#list span:nth-child(2) {
  margin-right: 10px;
  flex: 1;
}
#list span:nth-child(3) {
  margin-right: 10px;
  flex: 1;
}
#list span:nth-child(4) {
  flex: 1.5;
}
/* @media screen and (min-width: 992px) {
} 中型设备（台式电脑，992px 以上） */

@media screen and (max-width: 1000px) {
  #img {
    /* transform: translateX(100%);
    transition: 1s; */
    animation: xiaoshi 1s forwards;
    position: absolute;
    right: 0%;
  }
  #list {
    margin: 0px 8%;
  }
}
@media screen and (max-width: 600px) {
  #list {
    margin: 0px auto;
  }
}
@keyframes xiaoshi {
  from {
    transform: translateX(50%);
  }
  to {
    transform: translateX(100%);
  }
}
</style>